.appearance_page_wecodeart #wpcontent {
  padding-left: 0; }

.wecodeart-admin {
  color: #111; }
  .wecodeart-admin .components-panel {
    min-height: 100vh; }
    .wecodeart-admin .components-panel .components-panel__header {
      border: 0;
