        font-weight: 300;
        line-height: 1;
        margin-bottom: 10px; }
      .wecodeart-admin .components-panel .components-panel__header code {
        margin-left: 5px;
        border-radius: 2px;
        font-size: 11px;
        font-weight: 400;
        font-family: inherit;
        background-color: #e5e5e5;
        color: #000;
        opacity: 0.7; }
      .wecodeart-admin .components-panel .components-panel__header p {
