        margin-bottom: 50px; }
      .wecodeart-admin .components-panel .components-panel__header .wecodeart-panel__header-hint {
        font-size: 12px;
        opacity: 0.4;
        margin: 0 0 40px;
        padding: 0; }
    .wecodeart-admin .components-panel .components-panel__row {
      margin-top: 0; }
    .wecodeart-admin .components-panel .components-panel__body {
      padding: 0 50px 50px; }
  .wecodeart-admin .wecodeart-tab-panel {
    margin: 0 -50px;
