          .wecodeart-admin .wecodeart-tab-panel .components-tab-panel__tabs-item.active-tab, .wecodeart-admin .wecodeart-tab-panel .components-tab-panel__tabs-item.active-tab:hover {
            border-color: #0088cc;
            background: #fff; }
    .wecodeart-admin .wecodeart-tab-panel .components-tab-panel__tab-content {
      padding: 0 50px; }
      .wecodeart-admin .wecodeart-tab-panel .components-tab-panel__tab-content p,
      .wecodeart-admin .wecodeart-tab-panel .components-tab-panel__tab-content button {
        font-size: 16px;
        line-height: 1.5; }
    .wecodeart-admin .wecodeart-tab-panel--vertical {
