      margin: 0 0;
      width: auto; }
      .wecodeart-admin .wecodeart-tab-panel--vertical .components-tab-panel__tabs {
        flex-direction: column;
        padding: 15px 0 !important;
        border-left: 1px solid #e2e4e7;
        border-right: 1px solid #e2e4e7; }
        .wecodeart-admin .wecodeart-tab-panel--vertical .components-tab-panel__tabs-item {
          border: none; }
          .wecodeart-admin .wecodeart-tab-panel--vertical .components-tab-panel__tabs-item.active-tab {
            background-color: #e2e4e7;
            color: #000; }
      .wecodeart-admin .wecodeart-tab-panel--vertical .components-tab-panel__tab-content {
