        padding-top: 15px;
        padding-bottom: 15px;
        border-right: 1px solid #e2e4e7;
        border-bottom: 1px solid #e2e4e7; }
  .wecodeart-admin .wecodeart-button-field label {
    display: block;
    font-weight: bold; }
  .wecodeart-admin .wecodeart-button-field input {
    display: block;
    width: 100%;
    padding: 4px 8px; }
  .wecodeart-admin .wecodeart-button-group {
    display: inline-flex;
    align-items: center;
    margin: 0; }
    .wecodeart-admin .wecodeart-button-group .is-primary {
      padding: 0px 20px;
      font-size: 14px;
      margin-right: 10px;
      margin-bottom: 0; }
  .wecodeart-admin .wecodeart-license-manager {
