    .wecodeart-admin .wecodeart-license-manager td {
      border-top: 1px solid #e2e4e7; }
      .wecodeart-admin .wecodeart-license-manager td strong {
        color: #121212;
        font-size: 16px; }
  .wecodeart-admin .components-editor-notices__snackbar {
    position: fixed;
    left: auto;
    right: 0;
    bottom: 20px;
    padding-left: 16px;
    padding-right: 16px; }

.cm-s-default.CodeMirror,
.cm-s-default .CodeMirror-gutters {
