.cm-s-default span.cm-def, .cm-s-default span.cm-attribute, .cm-s-default span.cm-builtin, .cm-s-default span.cm-qualifier {
  color: #fae050; }

.cm-s-default span.cm-variable, .cm-s-default span.cm-variable-2, .cm-s-default span.cm-meta {
  color: #f8f8f2; }

.cm-s-default span.cm-variable-3, .cm-s-default span.cm-type {
